<template>
  <div>
    <demo-block title="基础" :card="true">
      <gw-button size="large" type="default" @click="onPopupShow('center')" >center</gw-button>
      <gw-button size="large" type="default" @click="onPopupShow('left')">left</gw-button>
      <gw-button size="large" type="default" @click="onPopupShow('right')">right</gw-button>
      <gw-button size="large" type="default" @click="onPopupShow('top')">top</gw-button>
      <gw-button size="large" type="default" @click="onPopupShow('bottom')">bottom</gw-button>
    </demo-block>
    <gw-popup
      v-model:show="show"
      :postion="postion"
      :mask="true"
      :overlay="true"
      @clickOverlay="onClickOverlay"
    >
      <div class="popup-content">
        内容
      </div>
    </gw-popup>
  </div>
</template>

<script>
import { Button, Popup } from 'gw-vue-ui'
export default {
  components: {
    GwButton: Button,
    GwPopup: Popup
  },
  data () {
    return {
      show: false,
      postion: 'center'
    }
  },
  methods: {
    onPopupShow (postion) {
      this.postion = postion
      this.show = true
    },
    onClickOverlay () {
      this.show = false
    }
  }
}
</script>

<style lang="scss" scoped>
.gw-button {
  margin-bottom: 10px;
}
.popup-content {
  width: 200px;
  height: 200px;
  background: #fff;
}
</style>
